const mapStateToProps = state => ({
  tasks: state.tasks,
});

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      generateTasks,
    },
    dispatch
  );

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Index);
